* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", sans-serif;
  /* min-height: 100%; */
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  /* min-height: 100%; */
  background: #161623;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f9fafc;
}

.todo-app {
  height: 10px;

  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-width: 50vmax;
  margin: auto;
  min-height: 500px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px !important;
  background-color: #42c3a7;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
  z-index: 1;
}

h1 {
  margin: 32px 0;
  color: #182748;
  font-size: 24px;
  font-family: "Nunito", sans-serif;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid;
  outline: none;
  width: 320px;
  background: transparent;
  color: #182748;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.todo-input::placeholder {
  color: #182748;
}

.todo-button {
  padding: 16px;
  border: none;
  margin-top: 10px;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
  background-color: #182748;
  color: #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.todo-button.edit {
  padding: 16px 22px;
}
.todo-button-add{
  padding: 16px;
  border: none;
  margin-top: 10px;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
  background-color: #182748;
  color: #fff;
}
.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}
.todo{
  display: -webkit-inline-box;
}
.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 8px auto;
  width: 100% !important;
  color: #182748;
  background: rgba(255, 118, 20, 0.5) 0%;
  border: 2px solid;
  padding: 12px;
  border-radius: 5px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.todo-row:nth-child(4n + 1) {
  background: rgba(93, 12, 255, 0.5) 0%;
  border: 2px solid;
}

.todo-row:nth-child(4n + 2) {
  background: rgba(250, 0, 135, 0.5) 100%;
  border: 2px solid;
}

.todo-row:nth-child(4n + 3) {
  background: rgba(20, 159, 255, 0.5) 0%;
  border: 2px solid;
}

.icons {
  display: flex-end;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  right:0 ;
}

.edit-icon,
.delete-icon {
  margin-right: 5px;
  color: #182748;
}

.delete-icon:hover {
  color: #182748;
}

.todo-content {
  align-self: center;
  max-width: 350px;
  margin-left: 10px;
}

.todo-list{
  height: 230px;
  width: 500px;
  overflow-y: scroll;
}

@media (min-width: 320px) and (max-width: 650px) {
  .todo-app {
    margin: 0;
    height: 100vh;
    width: 100vw;
  }

  .todo-context {
    width: 80vw;
  }

  .todo-input {
    width: 215px;
  }

  .todo-row {
    width: 80%;
  }

  .todo-button:hover {
    text-transform: none;
  }
}
